<template>
  <div class="avatar">
    <span v-if="hello">สวัสดี<br></span>
    <div v-if="user.line">
      <span>{{user.name}}</span><br>
      <b-avatar :src="user.line.pictureUrl" size="8rem"></b-avatar>
    </div>
    <div v-else>
      <span>{{user.displayName}}</span><br>
      <b-avatar :src="user.pictureUrl" size="8rem"></b-avatar>
    </div>
  </div>
</template>
<script>
export default {
  name: 'topMenu',
  props: {
    hello: {
      type: Boolean,
      required: true
    },
    user: {
      type: Object,
      required: true
    }
  }
}
</script>



