<template>
  <div>
    <div class="front_main text-center">
      <avatar :hello="true" :user="profile" v-if="profile"></avatar>
    </div>
  </div>
</template>
<style scoped>
.menu {
  padding: 0px 23px;
}
.menu > div.col-4 {
  padding: 6px;
}
.menu > div > img {
  max-width: 200px;
  max-height: 200px;
  min-width: 100px;
  min-height: 100px;
  width: 100%;
}
.iconNoti {
  position: absolute;
  top: 3px;
  right: 7px;
}
hr {
  background-color: #fff;
}
</style>
<script>
import Avatar from './Avatar.vue'
export default {
  name: 'Home',
  components: {
    'avatar': Avatar
  },
  data: () => ({
    pendingLeave: []
  }),
  async mounted () {
    await this.checkLiffLogin()
  },
  methods: {
    goto (url) {
      this.$router.push(url)
    }
  }
}
</script>



