// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import QrcodeVue from 'qrcode.vue'
import VueSession from 'vue-session'
import DatePicker from 'vuejs-datepicker'
import VueSweetAlert from 'vue-sweetalert'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

var options = {
  persist: true
}
Vue.use(VueSession, options)
Vue.use(BootstrapVue)
Vue.use(IconsPlugin)
Vue.use(VueSweetAlert)
Vue.prototype.$liff = window.liff
Vue.prototype.$apiUrl = 'https://asia-east2-vgroup-hrm.cloudfunctions.net/'
Vue.prototype.$webUrl = 'https://vgroup-hrm.firebaseapp.com/'
Vue.prototype.$axios = axios
Vue.prototype.$QrcodeVue = QrcodeVue
Vue.prototype.$DatePicker = DatePicker

Vue.prototype.$lineID = '@439qwfjt'
Vue.prototype.$liffId = '1655693870-mVgMeXdz'
Vue.prototype.$profile_dev = {
  'displayName': 'Pamorn Trivorrarat',
  'pictureUrl': 'https://profile.line-scdn.net/0hehdTWCiWOkdZLRKhl6VFEGVoNCouAzwPIUl2JX4pNnQnSHsUMRx8dCgoNCUmTS1BMRhzKHQpMyN9',
  'statusMessage': 'ใช้ไลน์อันนี้นะคร้าบ',
  'userId': 'U0157acd79f32f7abc5888d4fbb24f9ae'
}

Vue.config.productionTip = false

Vue.mixin({
  beforeCreate () {
    this.$liff.init({ 'liffId': this.$liffId }, function (data) {})
  },
  data: () => ({
    profile: {
      userId: '',
      displayName: '',
      pictureUrl: '',
      statusMessage: ''
    },
    me: null
  }),
  methods: {
    async checkLiffLogin () {
      await this.$liff.ready.then(async () => {
        // this.getProfile_dev()
        if (!this.$liff.isLoggedIn()) {
          this.$liff.login({redirectUri: window.location})
        } else {
          await this.getProfile()
        }
      })
    },
    async getProfile () {
      let _this = this
      await this.$liff.getProfile().then(function (profile) {
        console.log(profile)
        _this.profile = profile
      }).catch(function (error) {
        console.log('Error getting profile: ' + error)
      })
    },
    getProfile_dev () {
      this.profile = this.$profile_dev
    },
    displayDate (createdAt) {
      const date = new Date(Number(createdAt))
      return ('0' + date.getDate()).slice(-2) + '/' + ('0' + (date.getMonth() + 1)).slice(-2) + '/' + date.getFullYear()
    },
    formatDate (createdAt) {
      const date = new Date(Number(createdAt))
      return ('0' + date.getDate()).slice(-2) + '/' + ('0' + (date.getMonth() + 1)).slice(-2) + '/' + date.getFullYear() + ' ' + ('0' + date.getHours()).slice(-2) + ':' + ('0' + date.getMinutes()).slice(-2)
    },
    formatTime (createdAt) {
      const date = new Date(Number(createdAt))
      return ('0' + date.getHours()).slice(-2) + ':' + ('0' + date.getMinutes()).slice(-2)
    },
    liffLogout () {
      if (this.$liff.isLoggedIn()) {
        this.$liff.logout()
        window.location.href = '/'
      }
    }
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App, QrcodeVue, VueSession, DatePicker },
  template: '<App/>'
})



